<template>
  <div class="products">
    <div class="illness-t">
        <img :src="data.img" alt="">
        <div class="illness-l">
            <h3>{{ data.h3 }}</h3>
            <p>{{ data.p }}</p>
        </div>
    </div>
    <div class="homeContent-center">
      <mainLeft></mainLeft>
      <mainRight2></mainRight2>
    </div>
  </div>
</template>

<script>
import { products } from "~/data/index";
import mainLeft from "~/components/mainLeft.vue";
import mainRight2 from "~/components/mainRight2.vue";

export default {
  data() {
    return {
      data: {},
      products: products
    };
  },
  methods: {
    history() {
      history.go(-1);
    }
  },
  created() {
    this.data = this.products[1];
    window.scrollTo(0, 0);
  },
  components: {
    mainLeft,
    mainRight2
  }
};
</script>

<style lang="scss" scoped>
.products {
  width: 1200px;
  margin: 0 auto;
  margin-bottom: 100px;
  h1 {
    width: 100%;
    padding: 35px 0;
    font-weight: 400;
    span {
      float: right;
      font-size: 18px;
      line-height: 18px;
      cursor: pointer;
      padding: 13px 0;
      color: #03a57b;
    }
  }
  .illness-t:after {
    content: "\0020";
    display: block;
    height: 0;
    clear: both;
  }
  .illness-t {
    width: 100%;
    background: #fff;
    margin-bottom: 30px;
    img {
      float: left;
      width: 400px;
    }
    .illness-l {
      overflow: hidden;
      padding: 0 50px;
      h3 {
        margin-bottom: 20px;
        margin-top: 80px;
        font-size: 23px;
        letter-spacing: 3px;
      }
      p {
        opacity: 0.6;
        line-height: 23px;
        line-height: 29px;
      }
    }
  }
}
</style>



